@import "../../styles/global";
.wrapper{
    display: grid;
    grid-template-columns: repeat(2, 550px);
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    margin: 20px 0;
}
.block{
    display: grid;
    grid-template-columns: 180px auto;
    column-gap: 30px;
    padding: 40px 35px;
    img{
        width: 180px;
        height: 180px;
        object-fit: cover;
    }
    .wrap_block{
        display: grid;
        grid-template-rows: minmax(29px, auto) 90px 38px;
        row-gap: 10px;
    }
    p:nth-of-type(1) {
        font-style: normal;
        font-weight: 700;
        font-size: 22px;
        line-height: 26px;
        color: #000000;
        text-transform: uppercase;
    }
    p:nth-of-type(2) {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        padding-top: 10px;
        padding-bottom: 20px;
    }
    .wrap_btn{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

.random{
    background: $dark;
    padding: 40px 35px;
    position: relative;
    p{
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        color: #FFFFFF;
    }
    p:nth-of-type(1){
        padding-bottom: 30px;
    }
    p:nth-of-type(2){
        padding-bottom: 30px;
    }
    img:nth-of-type(1){
        position: absolute;
        bottom: 14px;
        right: 64px;
    }
    img:nth-of-type(2){
        position: absolute;
        bottom: 14px;
        right: -37px;
    }
}




