$primary: #9F0013;
$dark: #232222;
$grey: #5C5C5C;

  *{
    margin: 0;
    padding: 0;
    font-family: 'Roboto Condensed';
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  button{
    cursor: pointer;
  }
  a
  {
    text-decoration: none;
    color: inherit;
  }
  ul, li
  {
    list-style: none;
    padding: 0;
  }

